import { DownloadOutlined, UploadOutlined } from "@ant-design/icons";
import { Button, Form, message, Modal, Upload } from "antd";
import designationFile from "assets/data/designation.xls";
import { ACCEPT_FILE_LIST, CONFIRM_MESSAGE, SUBMIT_TEXT } from "constants/CommonConstants";
import useIntlMessage from "hooks/useIntlMessage";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { showLoader } from "redux/actions/Loader";
import manageDesignation from "services/DesignationService";
import { CANCEL_TEXT, VALIDATION_FAILED } from "../../../../constants/AddSubDomainConstants";

const UploadDesignation = ({ visible, onCancel }) => {
  const getMessage = useIntlMessage();
  const dispatch = useDispatch();
  const [file, setFile] = useState(null);
  const [form] = Form.useForm();
  const { confirm } = Modal;

  const handleConfirm = () => {
    confirm({
      title: CONFIRM_MESSAGE.value,
      centered: true,
      onOk() {
        onCancel();
      },
    });
  };

  const Cancel = () => {
    if (form.isFieldsTouched()) {
      handleConfirm();
    } else {
      onCancel();
    }
  };

  const uploadFile = async info => {
    try {
      dispatch(showLoader(true));
      let formData = new FormData();
      formData.append("XLSFILE", info.file);
      const response = await manageDesignation.uploadDocument(formData);
      if (response?.statusCode === 200) {
        if (response.records?.existingDesignationList.length !== 0) {
          dispatch(showLoader(false));
          message.warning(response.records?.existingDesignationList?.map(x => x?.message));
        } else {
          dispatch(showLoader(false));
          message.success(response?.message);
        }
      } else {
        dispatch(showLoader(false));
        message.error(response?.message);
      }
    } catch (error) {
      console.error(error);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const handleOk = () => {
    form
      .validateFields()
      .then(value => {
        uploadFile(file);
        onCancel();
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const handleFileUpload = async info => {
    setFile(info);
  };

  return (
    <Modal
      visible={visible}
      title={getMessage("common.uploaddesignation.tiltle")}
      onCancel={Cancel}
      onOk={handleOk}
      footer={[
        <Button onClick={Cancel}>{CANCEL_TEXT.text}</Button>,
        <Button key={SUBMIT_TEXT.value} onClick={handleOk} disabled={!file}>
          {getMessage("common.upload.titile")}
        </Button>,
      ]}
    >
      {
        <div className="mb-5 text-center">
          <span className="downlaod-button">
            <DownloadOutlined className="mr-1" />
            <a href={designationFile} target="_blank" download="sample-designations-data.xlsx" rel="noreferrer">
              {getMessage("components.dataEntry.sample.File")}
            </a>
          </span>
        </div>
      }
      <Form form={form} onFinish={handleOk} layout={"horizontal"}>
        <Form.Item className="text-center">
          <Upload
            className="ant-btn mr-2 w-50"
            maxCount={1}
            multiple={false}
            beforeUpload={() => false}
            showUploadList={true}
            onChange={handleFileUpload}
            accept={ACCEPT_FILE_LIST}
          >
            {<UploadOutlined className="mr-2" />}
            {getMessage("components.dataEntry.Choose.File")}
          </Upload>
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default UploadDesignation;
